.magazine {

	header {
		background: transparent;
		position: absolute;
		top: 31px;
		width: 100%;
		z-index: 3;
	}
}

.section--magazine-hero {
	height: 100vh;
	background-size: cover !important;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;

	&:before {
		content: '';
		display: block;
		position: absolute;
		background: rgba(0,0,0,0.5);
		background: -moz-linear-gradient(left, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%);
		background: -webkit-gradient(left top, right top, color-stop(0%, rgba(0,0,0,0.5)), color-stop(100%, rgba(0,0,0,0)));
		background: -webkit-linear-gradient(left, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%);
		background: -o-linear-gradient(left, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%);
		background: -ms-linear-gradient(left, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%);
		background: linear-gradient(to right, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#000000', GradientType=1 );
		height: 100%;
		width: 100%;
	}

	.caption {
		color: #fff;
		max-width: 400px;
		margin-left: 100px;

		@include media-breakpoint-down(sm) {
			max-width: 100%;
			margin-left: 0;
		}

		.abstract {
			font-size: 14px;
			line-height: 20px;
			letter-spacing: 0.5px;
			font-weight: $font-semibold;
			text-transform: uppercase;
		}

		.title {
			font-size: 36px;
			line-height: 54px;
			font-weight: $font-semibold;
			margin: 20px 0;
		}

		.description {
			font-size: 18px;
			font-weight: $font-regular;
			line-height: 30px;
		}
	}
}

.section--magazine-content {
	padding: 100px 0;

	p {
		width: 40%;
		position: relative;
		margin: 20px auto;

		@include media-breakpoint-down(md) {
			width: 80%;
		}

		@include media-breakpoint-down(sm) {
			width: 80%;
		}

		@include media-breakpoint-down(xs) {
			width: 100%;
		}

		&.quote {
			font-size: 36px;
			line-height: 54px;
			color: $foreground-primary;
			font-weight: $font-regular;
			text-align: center;
			width: 50%;
			margin: 100px auto;

			@include media-breakpoint-down(md) {
				width: 80%;
				font-size: 24px;
				line-height: 42px;
			}
		}
	}

	.img-news {
		position: relative;
		margin: 100px 0;
		display: block;
		text-align: center;

		img {
			max-width: 100%;
		}

		small {
			display: block;
			font-size: 16px;
			font-weight: $font-regular;

			a {
				color: $foreground-primary;
				text-decoration: underline;
			}
		}
	}

	.row {
		width: 80%;
		position: relative;
		margin: 100px auto;

		div {
			text-align: center;

			img {
				max-width: 100%;
			}
		}
	}
}

.section--magazine-inclinazioni {
	padding: 100px 0;
	background: #efefef;

	@include media-breakpoint-down(xs) {
		padding: 50px 0;
	}

	.intro {
		display: flex;
		flex-direction: column;
		justify-content: center;
		text-align: center;
		margin-bottom: 100px;

		.title {
			font-weight: $font-semibold;
			font-size: 36px;
			line-height: 54px;
			color: $foreground-primary;

			@include media-breakpoint-down(xs) {
				font-size: 28px;
				line-height: 36px;
				margin-bottom: 20px;
			}
		}
	}

	.box {
		background-size: cover !important;
		min-height: 700px;
		display: flex;
		align-items: flex-end;

		@include media-breakpoint-down(md) {
			min-height: 450px;
		}

		@include media-breakpoint-down(sm) {
			margin-bottom: 30px;
		}

		.black-layer {
			content: '';
			display: block;
			height: 200px;
			width: 100%;
			position: relative;
			z-index: 1;
			bottom: 0; left: 0;
			background: rgba(0,0,0,0);
			background: -moz-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
			background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(0,0,0,0)), color-stop(100%, rgba(0,0,0,1)));
			background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
			background: -o-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
			background: -ms-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
			background: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#000000', GradientType=0 );
		}

		.caption {
			position: absolute;
			z-index: 2;
			padding: 30px;

			.title {
				color: #fff;
				font-size: 40px;
				font-weight: $font-medium;
			}

			.description {
				color: #fff;
				font-size: 18px;
				line-height: 36px;
				letter-spacing: 0.2px;

				a {
					color: $foreground-primary;
					border-bottom: 2px solid $foreground-primary;
				}
			}
		}
	}
}